html {height: 100%}
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(red), to(transparent)),
	  -webkit-gradient(linear, right bottom, left top, from(lime), to(transparent)),
	  -webkit-gradient(linear, left bottom, right top, from(blue), to(transparent));
  background: -webkit-linear-gradient(red, transparent),
	  -webkit-linear-gradient(bottom right, lime, transparent),
	  -webkit-linear-gradient(bottom left, blue, transparent);
  background: -o-linear-gradient(red, transparent),
	  -o-linear-gradient(bottom right, lime, transparent),
	  -o-linear-gradient(bottom left, blue, transparent);
  background: linear-gradient(red, transparent),
	  linear-gradient(to top left, lime, transparent),
	  linear-gradient(to top right, blue, transparent);
  background-blend-mode: screen;	  
  background-repeat: no-repeat;
}


